/*global chrome*/
import React, {useEffect, useState} from "react";
import dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";
import duration from "dayjs/plugin/duration";
import DeadlineVisualizer from "./deadlineVisualizer";
import Welcome from "./welcome";

dayjs.extend(duration);
dayjs.extend(relativeTime);

const parseData = deadlines => {
    const random = Math.ceil(Math.random() * 100)
    const index = random % deadlines.length

    const deadline = deadlines[index];

    let startDate = dayjs(deadline.start);
    let endDate = dayjs(deadline.end);
    let summaryUnit = "days";
    let diff = dayjs.duration(endDate.diff(startDate));
    let numOfBoxes = Math.ceil(diff.asDays());
    if (numOfBoxes > 30) {
        summaryUnit = "weeks";
        numOfBoxes = Math.ceil(diff.asWeeks());
    }

    console.log(summaryUnit, numOfBoxes);

    const now = dayjs();

    let passedCount = 0;

    let boxes = [];

    for (let i = 1; i <= numOfBoxes; i++) {
        const remainingTime = startDate.from(now);
        const passedTime = now.from(startDate);

        if (startDate.isAfter(now)) {
            boxes.push({passed: false, info: remainingTime});
        } else {
            passedCount++;
            boxes.push({passed: true, info: passedTime});
        }

        if (summaryUnit === 'days') {
            startDate = startDate.add(1, 'day');
        } else {
            startDate = startDate.add(7, 'day');
        }
    }

    const remainingDays = numOfBoxes - passedCount;
    const percentage = (passedCount / numOfBoxes * 100).toFixed(2);

    return {
        name: deadline.name,
        boxes,
        summary: `${remainingDays} ${summaryUnit} remaining (${percentage}%)`
    }
}

const Visualizer = () => {
    const [deadline, setDeadline] = useState({});
    const [deadlineExists, setDeadlineExists] = useState(false);

    useEffect(() => {
        chrome.storage.sync.get('deadlines', data => {
            if (!!data && !!data.deadlines) {
                setDeadline(parseData(data.deadlines));
                setDeadlineExists(true);
            }
        })
    }, []);

    return deadlineExists ? <DeadlineVisualizer deadline={deadline} /> : <Welcome />;
}

export default Visualizer;